import React, { Component } from 'react';
import Jumbotron from 'react-bootstrap/Jumbotron';
import Container from 'react-bootstrap/Container';
import AddButton from './components/AddButton';
import GroupList from './components/GroupList';

import './App.css';

class App extends Component {
  constructor() {
    super();
    this.state = {
      currentTodo: 'Write a note',
      todoList: ['Check the list']
    };
  }

  handleSubmit = $event => {
    const randomListToDo = [
      'Shopping',
      'Wash the Dishes',
      'Vacuum the Room',
      'Wipe the floor',
      'Go for a Walk',
      'Check the Mails',
      'Learn React',
      'Organise Paperwork',
      'Plans for the Weekend',
      'call my Cousin',
      'Work out'
    ];
    const randomThingToDo =
      randomListToDo[Math.floor(Math.random() * randomListToDo.length)];
    $event.preventDefault();
    this.setState(oldState => {
      return {
        todoList: [...oldState.todoList, oldState.currentTodo],
        currentTodo: randomThingToDo
      };
    });
  };

  handleOnChangeCurrentTodo = value => {
    this.setState(() => {
      return {
        currentTodo: value
      };
    });
  };

  render() {
    return (
      <div className="containter" bg="light">
        <Jumbotron>
          <Container>
            <div className="text-center">
              <h1 className="title">Todo List</h1>
              <form onSubmit={this.handleSubmit}>
                <AddButton
                  value={this.state.currentTodo}
                  onChange={this.handleOnChangeCurrentTodo}
                />
              </form>
              <GroupList list={this.state.todoList} />
            </div>
          </Container>
        </Jumbotron>
      </div>
    );
  }
}

export default App;
